.representatives {
  height: 90vh;
  position: relative;
  margin-bottom: 50px;
}
.reps-container {
  display: flex;
}
.map {
  width: 40%;
}
.map-info {
  width: 60%;
}
.map-info-container {
  border: 1px #002868 solid;
  border-radius: 3px;
  margin-right: 50px;
  display: inline-block;
  margin-top: 40px;
  padding: 30px 50px;
}
.map > svg {
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  filter: drop-shadow(0 0 5px black);
}
.map-okrag-number {
}
.map-okrag-name {
  white-space: pre-wrap;
  font-size: 26px;
  line-height: 50px;
}

.map > svg path:hover {
  stroke: #002868 !important;
  stroke-width: 2px;
  stroke-linejoin: round;
  fill: #002868 !important;
  cursor: pointer;
}

.button-container {
  font-size: 14px;
  text-align: center;
  position: relative;
}

.button-text-box {
  text-align: center;
  color: white;
  letter-spacing: 1px;
  text-transform: uppercase;
  background-color: darkblue;
  color: white;
  margin-top: 15px;
  border-radius: 4px;
  padding: 9px 15px;
  transition: 0.3s;
}

.button-text-box:hover {
  cursor: pointer;
  opacity: 0.8;
  transform: scale(1.05);
}

.button-text-box:active {
  transform: scale(0.95);
}
